		#arrow,
		#arrow::before {
		  position: absolute;
		  width: 8px;
		  height: 8px;
		  background: inherit;
		}

		#arrow {
		  visibility: hidden;
		}

		#arrow::before {
		  visibility: visible;
		  content: '';
		  transform: rotate(45deg);
		}

		#Tooltip[data-popper-placement^='top'] > #arrow {
		  bottom: -1rem;
		}

		#Tooltip[data-popper-placement^='bottom'] > #arrow {
		  top: -1rem;
		}

		#Tooltip[data-popper-placement^='left'] > #arrow {
		  right: -1rem;
		}

		#Tooltip[data-popper-placement^='right'] > #arrow {
		  left: -1rem;
		}


